import { useState } from "react";
import ModalMatrial from './Modal'
import Button from '@mui/material/Button';

function App() {
  const [state, setState] = useState([{
    name: "mobina",
    id: 1,
    phone: '0912'
  }, {
    name: "kimia",
    id: 2,
    phone: '0911'
  }, {
    name: "mohi",
    id: 3,
    phone: '0902'
  }]);

  function delet(arg) {
    let newState = []
    let javab = window.confirm("do you want to delete this contact?")
    if (javab == true) {
      state.map(function (el, index) {
        console.log(index)
        if (arg == el.id) {

        } else {
          newState.push(el)
        }
      })
    }
    else {
      return state

    }
    setState(newState)
  }

  return (
    <section>
      <ModalMatrial userData={{ name: "", phone: "" }} updateState={setState} prevState={state} type="add" />
      {
        state.map(function (el, index) {
          return (
            <div key={el.id} className="card">
              <h1 style={{ margin: "0 10px" }}>{el.name}</h1>
              <h4 style={{paddingTop:"10px"}}>{el.phone}</h4>
              <div className="action-btns">
                {/* <button onClick={() => delet(el.id)} >delete</button> */}
                <Button onClick={() => delet(el.id)} variant="outlined" color="error">
                  Delete
                </Button>
                <ModalMatrial userData={el} updateState={setState} prevState={state} type="edit" />
              </div>
            </div>
          )
        })
      }
    </section>

  )
}
export default App;