import * as React from 'react';
import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  '& .MuiDialogContent-root': {
    padding: theme.spacing(2),
  },
  '& .MuiDialogActions-root': {
    padding: theme.spacing(1),
  },
}));

const BootstrapDialogTitle = (props) => {
  const { children, onClose, ...other } = props;


  return (
    <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
        </IconButton>
      ) : null}
    </DialogTitle>
  );
};

BootstrapDialogTitle.propTypes = {
  children: PropTypes.node,
  onClose: PropTypes.func.isRequired,
};

export default function ModalMatrial(props) {
  const [open, setOpen] = React.useState(false);
  const [phoneInput, setPhoneInput] = React.useState(props.userData.phone)
  const [nameInput, setNameInput] = React.useState(props.userData.name)

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const handleNewUser = () => {
    console.log(phoneInput, "phoneInput");
    console.log(nameInput, "nameInput");
    let newState2 = []
    // let javab2 = window.prompt("please enter new contact")
    newState2.push({
      name: nameInput,
      phone : phoneInput,
      id: props.prevState.length + 1
    })
    for (let index = 0; index < props.prevState.length; index++) {
      const element = props.prevState[index];
      newState2.push(element)

    }
    props.updateState(newState2)
    setOpen(false);
  }

  const handleSubmit = () => {
    setOpen(false);
    let newState1 = []
    props.prevState.map(function (el, index) {
      if (props.userData.id == el.id) {
        newState1.push({
          name: nameInput,
          id: el.id,
          phone : phoneInput
        })
      }
      else {
        newState1.push(el)
      }
    })
    props.updateState(newState1);
    setOpen(false);
  };


  return (
    <>
    {/* <button onClick={handleClickOpen} >{props.type =="add" ? "Create new User" : "Edit modal"}</button> */}
    <Button style={{marginLeft : "10px"}} variant={props.type =="add" ? "contained" : "outlined"} onClick={handleClickOpen} >{props.type =="add" ? "Create new User" : "Edit"}</Button>
    <div>
      <BootstrapDialog
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={open}
      >
        <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
          {props.type == "add" ? "Create Data" : "Edit Data"}
        </BootstrapDialogTitle>
        <DialogContent dividers>
          {props.type == "edit" ?<> 
          <h1 style={{marginBottom:"20px"}}>{props.userData.name}</h1> </> : ""}
          <TextField id="filled-basic" label="Name User" variant="filled" value={nameInput} onChange={(e) => setNameInput(e.target.value)} /> <br />
          <TextField style={{ margin: '30px 0' }} id="filled-basic" label="Phone number" variant="filled" value={phoneInput} onChange={(e) => setPhoneInput(e.target.value)} />
        </DialogContent>
        <DialogActions>
          {props.type == "add" ? <Button autoFocus onClick={handleNewUser}>
            make new one
          </Button> : <Button autoFocus onClick={handleSubmit}>
            Save changes
          </Button>}
        </DialogActions>
      </BootstrapDialog>
    </div>
    </>
  );
}